<script>
export default {
  mounted() {
    this.get_energy_all();
  },
  data() {
    return {
      year: "",
      items: [],
    };
  },
  methods: {
    get_energy_all() {
      this.year = new Date().getFullYear();
      this.axios({
        method: "GET",
        url:
          this.$store.state.url_axios +
          "api/v1/energy/mm_600194433DCF?year=" +
          this.year,
        headers: { Authorization: "Bearer " + localStorage.token },
      }).then((response) => {
        // console.log(response.data.result);
        this.items = response.data.result;
      });
    },
  },
};
</script>

<style>
.font {
  font-size: 20px;
}
